.main-container {
  padding-top: 0vw;
}
body { margin: 0;}

.sticky {
  background: black;
  color: white;
  width: 100%;
  padding: 0px 0px;
}

.sticky {
  margin-bottom: 150px;
  position: absolute;
  bottom: 0px;
  transform: translateY(-100%);
  width: 100%;
}
a {
  color: #555;
  font-weight: 400;
}

.pSection {
  position: relative;
  
}

.container {
  padding: 20px;
  margin: 0 auto;
  max-width: 1070px;
  position: relative;
  z-index: 1;
}

.pContent {
  width: 65%;
  background-color: rgba(78,152,21,0.8);
  color: white;
  padding: 40px 60px;
}

.pImage {
  height: 100vh; 

  position: absolute;
  top: 0;
  right: 0;
}